    title: 'Дожим клиента: 28 способов продавать день в день',
    rating: 3,
    issueYear: '2019',
    description:
      'Как часто вы видите в своих финансовых отчетах суммы с шестью и более нулями? Скорее всего, это было давно и неправда. Значит, пришло время учиться продавать больше, быстрее и эффективнее. А точнее, с первого звонка.\nВспомните, сколько времени проходит у вас между первым контактом с клиентом и заключением сделки. В зависимости от сферы работы считается удачным, если сделка заключена в период от двух недель до трех месяцев. В этой книге собраны способы продавать день в день. С примерами, скриптами разговоров, действующими способами дожима покупателей.\nЕсли вас смущает слово «дожим», то можете не волноваться. Под ним подразумевается всего лишь максимально успешное воздействие на клиента. При этом никакие нормы морали и законодательства не нарушаются. Вы научитесь брать своим обаянием, умением договариваться, грамотно проводить встречи и получать от человека то, что вам нужно.\nУспехов вам в продажах!',
