    description:
      'Владимир Якуба - бизнес-тренер и Продажник с большой буквы. Он любит повторять, что провёл обучение в 112 городах, 17 странах, а также является автором 8 книг, 11 обучающих фильмов, более 300 публикаций в 42 изданиях. И после переспрашивает: запомнили, сколько было стран? И его слушатели запоминают. \r\nТак действуют и методы Владимира Якубы - простые, понятные, много раз отработанные на практике. Владимир учит продавать на любом уровне и на любых площадках: гендиректору и рядовому менеджеру, по телефону и «в полях». В своей новой книге Владимир собрал самые крутые стратегии: освойте их - и вам открыта дорога в клуб профессионалов, «продажников на всю голову»!\r\n',
