    description:
      'Маркетинг услуг - одна из наиболее сложных сфер профессиональной деятельности.\nВы не можете дотронуться до основного продукта своей компании, услышать его или увидеть... Тогда как же организовывать продажи и стимулировать сбыт? Книга Гарри Беквита, основанная на двадцатипятилетнем опыте работы автора с тысячами профессиональных бизнесменов, доносит до читателей маркетинговые знания через рассмотрение практических примеров работы как крупных компаний, таких как Federal Express и Citicorp, так и небольших частных предприятий. Книга лаконична, увлекательна и содержит сотни практичных и легких для реализации приемов и стратегий, которые используются в сфере маркетинга услуг.\nИздание ориентировано на специалистов в области маркетинга услуг, а также студентов и преподавателей экономических вузов.',
