    title: 'Деньги делают деньги: От зарплаты до финансовой свободы',
    rating: 3,
    issueYear: '2020',
    description:
      'Одна из составляющих свободы — финансовая независимость. Доход финансово свободного человека позволяет ему не думать постоянно о деньгах как о средстве существования. Дмитрий Лебедев написал эту книгу на основании собственного опыта обретения финансовой свободы и опыта своих клиентов, друзей и знакомых. Все примеры в ней реальны, все рекомендации конкретны и проверены.\n\nКуда можно и нужно вкладывать деньги, а куда — слишком рискованно? Что приносит доход и как сбалансировать свои вложения в разные виды активов? Чем активы отличаются от пассивов и какие опасности подстерегают человека с деньгами и российским паспортом?Эта книга поможет вам выбраться из круга постоянной нехватки денег. Автор научит, как создать личный капитал, сохранить его и приумножить, чтобы гонка за деньгами перестала быть первым пунктом повестки дня, чтобы можно было по-настоящему жить, отдыхать, путешествовать, помогать другим — делать то, что вам нравится и к чему вы готовы.',
