    id: 22,
    title: 'Big Money: принципы первых. Откровенно о бизнесе и жизни успешных предпринимателей',
    rating: null,
    issueYear: '2020',
    description:
      '«Кто не хочет, ищет причины. Кто хочет, ищет возможности - фраза моего Отца, главная фраза в моем воспитании». Эта книга о людях, которые всегда ищут возможности и находят их. Всё хорошее в этом Мире придумано людьми, которые хотели заработать. Предпринимателями и Бизнесменами. Эта книга о них. О тех, кто каждый день делает этот мир лучше, развивая свой бизнес, создавая рабочие места и платя налоги.\n\nВопреки стереотипам, это не скучные, угрюмые капиталисты, у которых в голове только деньги. Это интересные, умные, харизматичные люди, которые, как и каждый из нас, каждый день нуждаются в успехе и сожалеют о том, что времени для этого всегда не достаточно. Их отличие только в том, что все они одержимые своей идеей перфекционисты. А именно таким будет принадлежать Мир будущего. Я очень волнуюсь. В твоих руках, уважаемый читатель, моя первая книга, состоящая из интервью успешных предпринимателей в YouTube - проекте Big Money.\n\nКнига о людях и их историях побед. Эта книга поможет тебе увеличить скорость и быстрее достигнуть своей мечты. Именно для этого эта книга и создана!\n\nЕвгений Черняк',
