    rating: null,
    issueYear: '2021',
    description:
      'Эта книга о правильной и понятной коммуникации: как с помощью слов доносить свои мысли и влиять на людей. Книга адресована всем, кто уже умеет писать и сокращать, но хочет делать это лучше - так, чтобы не только информировать, но и убеждать словом, - копирайтерам, маркетологам, бизнес-тренерам, предпринимателям и блогерам.\nМаксим Ильяхов развивает тему, затронутую в бестселлере "Пиши, сокращай". Вы узнаете, как привлечь внимание читателей, как помочь им понять и принять точку зрения автора, как увлекательно и доступно раскрыть любую тему, даже самую сложную. Вы найдете в книге более 50 инструментов улучшения текста. В разделе "Контекст" автор покажет, как справиться с предвзятостью читателей и настроить их на верное восприятие информации. В разделе "Интерес" - как превратить скучный текст в увлекательный, не меняя его содержания. Раздел "Текст" посвящен использованию примеров, антипримеров, аналогий, метафор, сюжетности и слоганов - тому, что поможет адресату правильно понять написанное вами. В разделе "Подача" представлены инструменты мгновенного воздействия: как донести главное заголовком, схемой или фотографией. Максим Ильяхов рассказывает о создании сильного текста, подкрепляя теорию примерами из собственной практики.',
