    description:
      'Стив Бланк, гуру стартап - движения, говорит, что главное для начинающего предпринимателя - это "выйти из офиса", то есть начать напрямую узнавать у клиентов, что им нужно. Однако получите ли вы действительно важную информацию, зависит от того, какие вопросы вы будете задавать. Кстати, самый популярный вопрос - "Нравится ли вам наша идея или продукт?" - неверен. Это все равно что спрашивать маму, по душе ли ей ваша идея: она любит вас и в любом случае похвалит, не желая расстраивать. Так же поступают и 99% клиентов. В общем, чтобы быть успешным предпринимателем, надо знать, как и что именно спрашивать, и по книге Роба Фитц­патрика вы научитесь это делать!\n\nАвтор поможет вам понять, когда ответ дается из вежливости и не несет никакой ценности, подскажет, в каком направлении следует развивать беседу, какие уточняющие вопросы задавать, как избегать социально желаемых ответов, как понять, что в продукте хорошо или плохо, нужен ли он рынку, как его правильно позиционировать - и все это со множеством реальных примеров.\n\nПеред вами краткое, полезное и написанное с хорошим юмором практическое руководство по эффективному общению предпринимателя с клиентами. Оно поможет вам сэкономить время, деньги и нервы.',
