    rating: null,
    issueYear: '2017',
    description:
      'Huawei - один из флагманов новой китайской экономики. Почти за тридцать лет своего существования компания превратилась из мелкого продавца АТС в ведущего разработчика инновационных решений в сфере информационно-коммуникационных технологий. Офисы компании открыты в 170 странах мира, а ее продукцией пользуется треть населения земного шара. Сегодня Huawei - единственная из частных китайских компаний, которая входит в топ-150 международного рейтинга Fortune.\n\nПоразительный успех Huawei основан на трех ценностях: центральной роли клиента, самоотдаче сотрудников и их преданности делу. Со временем в компании сложился уникальный стиль управления, сочетающий в себе особенности восточной и западной моделей. А главное, с первого дня существования Huawei ее основатель Жэнь Чжэнфэй строил компанию, ориентированную на перспективу, в которой каждый нацелен на результат.\n\nЭта книга - итог более чем десятилетнего тщательного исследования. Автор изучил тысячи страниц документов, провел 136 интервью с руководителями и рядовыми сотрудниками Huawei, чтобы дать объективную картину восхождения китайской компании к вершинам мирового бизнеса.',
