    rating: null,
    issueYear: '2020',
    description:
      '"Бизнес без MBA" — первая в России книга, которая закрывает разрыв между сухой теорией и голым вдохновением.\nЗдесь собраны практические советы для тех, кто только начинает или уже начал своё дело — торговлю, разработку или услуги, в сегменте B2B или B2C. Здесь описаны самые важные вещи, которые помогут предпринимателю на старте: финансы, маркетинг, переговоры, клиентский сервис, отношения с партнерами и банками.\nВсе советы и ситуации взяты из российской практики, на примере российского малого бизнеса. Здесь нет не связанной с жизнью теории и пустых слов вдохновения: только те знания, которые помогут защитить и развивать бизнес в суровых российских условиях.\nАвторы — эксперты Тинькофф Банка по финансам, экономике, маркетингу и управлению. Это люди, которые каждый день помогают малому бизнесу и знают его проблемы. Редактор — Максим Ильяхов, соавтор бестселлеров "Пиши, сокращай" и "Новые правила деловой переписки".\nКнига будет полезна всем, кто хочет вложить деньги в бизнес и получить прибыль, несмотря на сложные законы, непростые отношения с партнерами и жесткую конкуренцию.',
