    description:
      'За одно только последнее десятилетие человечество пережило ряд тяжелейших катастроф, потрясений и катаклизмов, не укладывающихся в рамки самых фантастических предсказаний. Пятидесятидвухлетний ливанец, выпускник Сорбонны и нью-йоркский финансовый гуру Нассим Талеб называет такие непрогнозируемые события Черными лебедями. Он убежден: именно они дают толчок как истории в целом, так и существованию каждого отдельного человека. И чтобы преуспеть, надо быть к ним готовыми.\r\nСразу после выхода "Черного лебедя" автор блестяще продемонстрировал свою "не-теорию" на практике: на фоне финансового кризиса компания Талеба заработала (а не потеряла!) для инвесторов полмиллиарда долларов. Но его труд - не учебник по экономике. Это размышления очень незаурядного человека о жизни и о том, как найти в ней свое место. В написанном им позже эссе-постскриптуме "О секретах устойчивости" Талеб дает остроумный отпор тем экономистам-ортодоксам, которые приняли созданное им анти-учение в штыки.\r\nВошедшее в книгу собрание афоризмов Нассима Талеба - это блестящая квинтэссенция его оригинальных идей.\r\n"Черный лебедь" семнадцать недель держался в списке бестселлеров "New York Times" и переведен на 27 языков.',
