    description:
      'Последние двадцать лет нас учили, что деловая переписка - это бесконечные реверансы, бессмысленные формы вежливости и вымученные шаблоны: «Коллеги, я вас услышал», «Клиент прислал правочки, внесите АСАП. Заранее спасибо», «С уважением к вам и вашему бизнесу».\r\nСовременное деловое письмо - это махровая фальшь, копеечные манипуляции и плохо скрываемое обоюдное презрение. Максим Ильяхов и Людмила Сарычева, авторы культовой книги «Пиши, сокращай», предлагают менять культуру делового письма и выдвигают два новых правила: уважение к адресату и забота о его интересах. Письма, составленные по таким правилам, располагают к вам людей и помогают находить общий язык.\r\nСледуя рекомендациям из книги, вы научитесь писать коммерческие предложения, сопроводительные письма, просьбы коллегам, ответы клиентам и любые другие рабочие письма. Это обязательная книга для всех, кто пишет по работе: руководителей, менеджеров, специалистов и соискателей.\r',
