    title: 'Сделай это завтра и другие секреты тайм-менеджмента',
    rating: 5,
    issueYear: '2020',
    description:
      '«Cделай это завтра» — одна из самых актуальных книг о тайм-менеджменте. «Не делай сегодня то, что можно отложить на завтра», — такой совет даёт Марк Форстер, американский тренер по личной эффективности. И он правда работает!\r\nВот, что пишет о «Cделай это завтра» Варя Веденеева, основательница 365done\r\n«Мне кажется, что слово тайм-менеджмент и принципы из GTD-книг очень сильно устарели. Изменился мир, изменились отвлекающие факторы, изменились некоторые принципы работы.\r\nМарк описал многое из того, что я интуитивно практикую в планировании. В мою работу и деятельность уже не новый тренд эссенциализма вписывается идеально: в сверхнасыщенном информационном поле очень сложно построить хоть какую-то стабильную систему. Срочные задачи летят из всех мессенджеров и сбивают с мыслей. Сконцентрироваться на одной задаче хотя бы 5 минут очень сложно.\r\nУже два года я живу без уведомлений — они выключены во всех мессенджерах; проверяю почту 2-3 раза в день, ставлю себе задачи на день. В этом году я научилась говорить „нет“ всем срочным делам и проектам — если клиент думал с сентября, а принял решение в декабре, я не буду браться за этот „горящий“ проект».\r\nВ книге описывается идея «закрытого списка задач», в который нельзя добавить новые пункты после его формирования, а также подчеркивается важность выстраивания в работе личных границ.',
