    rating: null,
    issueYear: '2021',
    description:
      'Эта книга — продолжение бестселлеров «Построение бизнес-моделей» и «Разработка ценностных предложений», переведенных более чем на 40 языков. Авторы предлагают уникальную методику тестирования бизнес-идей на этапе разработки. 44 эксперимента, представленных в книге, помогут вам выработать свой подход к масштабированию идей.Довольно часто заманчивые бизнес-идеи в ходе реализации терпят полный крах. Тестирование на этапе разработки идеи — это способ снижения риска реализации идей, блестящих на бумаге, но нежизнеспособных на рынке. Идеи тестируются посредством быстрых экспериментов, которые позволяют собрать мнения потенциальных покупателей и скорректировать бизнес-модель до запуска продукта на рынок.\r\nАвтор мировых бестселлеров «Построение бизнес-моделей» и «Разработка ценностных предложений» Алекс Остервальдер и консультант в сфере развития бизнеса Дэвид Блэнд научат вас тестировать идеи на уровне самых успешных стартапов мира. Они познакомят вас с концепцией, расскажут, как спланировать первый эксперимент и запустить процесс тестирования. Кроме того, вы узнаете, как через ритуалы и общий командный дух сделать тестирование идей обычной практикой и нормой ведения бизнеса всей организации.',
