    title: 'Непобедимая компания: Как непрерывно обновлять бизнес-модель вашей организации, вдохновляясь опытом лучших',
    rating: 4.13,
    issueYear: '2021',
    description:
      'Это четвертая книга из серии бестселлеров, переведенных более чем на 40 языков. В серию Strategyzer входят книги: «Построение бизнес-моделей», «Разработка ценностных предложений» и «Тестирование бизнес-идей».\r\nВы узнаете, как с помощью портфеля бизнес-моделей управлять компанией и совершенствовать ее, одновременно исследуя будущее. \r\nВы поймете, как разрабатывать, тестировать и внедрять передовые бизнес-модели, руководствуясь опытом самых успешных мировых организаций.\r\nВы узнаете, как улучшать основной бизнес и осваивать абсолютно новые возможности, выходящие за традиционные границы отрасли.\r\nВы сможете извлекать максимальную выгоду из возможностей рынка, новых технологий и инноваций в сфере товаров и услуг, используя шаблоны бизнес-моделей\r\nПросто быть лучшим уже недостаточно. Сегодня в конкурентной борьбе побеждают компании, у которых готовность к изменениям вшита в бизнес-модель. В четвертой книге серии Strategyzer Алекс Остервальдер и его соавторы Ив Пинье, Фред Этьембль и Алан Смит рассказывают о стратегиях адаптации успешных компаний, которые не раз перепридумывали свои бизнес-модели: Amazon, IKEA, Airbnb, Microsoft, Logitech, Zara и другие. На их примере вы научитесь формировать и применять портфель бизнес-моделей, внедрять и поддерживать культуру инноваций, чтобы непрерывно подпитывать свой бизнес новыми идеями и сделать его непобедимым.\r',
