    description:
      'Мы думаем, что мы думаем. \nНо это не так. В нас думает наш мозг. Он делает это в обход сознания, сам по себе. И мы даже не догадываемся об истинных причинах своих поступков. \nНаш мозг рисует картину реальности. Это он принимает решения, которые мы считаем своими. Это от него зависит, что будет с нами дальше. \nТаковы научные факты. Отрицать их бессмысленно. Их надо знать. \nИз этой книги вы узнаете, как мыслит ваш мозг и как заставить его работать на вас. Это книга о мозге и мышлении, о вашей жизни и о том, как сделать её лучше. \nИ да, для этого нам придётся убить в себе идиота. Действуйте смелее! Вам понравится...',
