    description:
      'Кто сказал, что перемены должны отнимать много сил? Если вы, как и многие купились на подобный миф, и из-за этого много раз бросали дело на полпути, спешим вас обрадовать. Вы не виноваты, это всего лишь ошибка в подходе, а не личный недостаток.\r\nНачните с двух отжиманий в день, а не с двухчасовой тренировки; или делайте пять глубоких вдохов каждое утро, а пытайтесь просидеть целый час в позе лотоса. Вы не поверите, но крошечные действия способны творить чудеса!\r\nДоктор Би Джей Фогг, ведущий мировой эксперт по формированию привычек собрал многолетний опыт, чтобы помочь вам похудеть, снять стресс, лучше спать или достичь любой другой цели.\r\n',
