    description:
      'Быть стоиком в современном мире не сложнее, чем в Древней Греции. Для этого надо иметь душевный покой, чтобы принимать то, что невозможно изменить, мужество — чтобы изменить возможное, и мудрость — всегда отличать одно от другого. В своей книге «Как быть стоиком» известный американский философ и биолог Массимо Пильюччи предлагает читателю увлекательное путешествие в глубины этой античной философии, а в качестве проводника выбирает древнегреческого стоика Эпиктета. Стоицизм — очень современная и в высшей степени прикладная философия, которая учит, как принимать верные решения, управлять гневом, воспринимать критику, как относиться к смерти — собственной и своих близких. Но главный вопрос, на который отвечает стоицизм, — как правильно жить.',
