    title: 'Говори  красиво и уверенно. Постановка голоса и речи',
    rating: 2,
    issueYear: '2021',
    description:
      'Что такое наш голос? Это прежде всего инструмент. Музыкальный, тонко устроенный, сложный, неповторимый. Можно быть серой мышкой с тихим, завышенным, неярким и плоским голосом, а можно играть первую скрипку в оркестре - каждый выбирает для себя. Задача этой книги - повышение КПД каждого человека через развитие голоса. Это инструмент успешности - мощный и уникальный. Книга поможет вам понять себя, свои желания, свой голос, настроить его и полюбить. Мы поэтапно, глава за главой, урок за уроком научим обращаться с этим волшебным инструментом и использовать его богатые возможности на все 100 или даже 200%.\nКнига построена по принципу тренинга, где в каждом блоке раскрывается новая тема. Между блоками есть связки, домашние задания и вопросы для самоконтроля. Вы сможете самостоятельно продиагностировать свой голос, а также ответить на вопрос, который интересует, пожалуй, большинство людей: а какой же у меня голос? С помощью необычных, интересных заданий и листов контроля вы сможете исправить многие недостатки, присутствующие в вашей речи, и закрепить полученный результат. Надеемся, что благодаря такому подходу вы сможете изменить свой голос, усовершенствовать его и победить страх перед аудиторией.',
