    id: 98,
    title: 'Атлант расправил плечи. Часть первая. Непротиворечие',
    rating: 3.17,
    issueYear: '2018',
    description:
      'Айн Рэнд (1905-1982) — наша бывшая соотечественница, ставшая крупнейшей американской писательницей. Автор четырех романов-бестселлеров и многочисленных статей. Создатель философской концепции, в основе которой лежит принцип свободы воли, главенство рациональности и «нравственность разумного эгоизма».\nЕе книги читает весь мир. В США она завоевала огромную популярность, ее романы переиздаются из года в год и по совокупности тиражей конкурируют с Библией. Всемирное признание Айн Рэнд нетрудно объяснить: исключительный дар предвидения в самых разных областях — политике, бизнесе, экономике, общественных отношениях — в сочетании с художественной одаренностью принес ей славу большого писателя и проницательного мыслителя. «Атлант расправил плечи», самое значимое произведение своей жизни, она писала 12 лет. В первой части читатели знакомятся с главными героями, гениальными предпринимателями, которым противостоят их антиподы — бездарные государственные чиновники. Повествование начинается с вопроса: «Кто такой Джон Голт?» — и на этот вопрос будут искать ответ герои романа и его читатели.',
