    title: 'Атлант расправил плечи. Часть третья. А есть А',
    rating: null,
    issueYear: '2018',
    description:
      'Айн Рэнд (1905-1982) — наша бывшая соотечественница, ставшая крупнейшей американской писательницей. Автор четырех романов-бестселлеров и многочисленных статей. Создатель философской концепции, в основе которой лежит принцип свободы воли, главенство рациональности и «нравственность разумного эгоизма».\r\nТретья часть романа «Атлант расправил плечи» развенчивает заблуждения мечтательных борцов за равенство и братство. Государственные чиновники, лицемерно призывающие граждан к самопожертвованию, но ограничивающие свободу предпринимательства, приводят страну к экономическому краху. Сюжет сплетается из финансовых и политических интриг, и одновременно звучит гимн новой этике: капиталистическая система ценностей не только социально оправданна, но и нравственна. Герой нового мира, гениальный изобретатель Джон Голт провозглашает принцип «нравственности разумного эгоизма» одной фразой: «Я никогда не буду жить ради другого человека и никогда не попрошу другого человека жить ради меня».\r\n',
