    rating: 3,
    issueYear: '2019',
    description:
      '«Большинство, глядя на плохо оформленную страницу, скажут, что она им не нравится, однако они не знают, как ее исправить. Эта книга не заменит учебу в школе дизайна. Я не утверждаю, что после ее прочтения вы станете дизайнером. Но я ручаюсь, что вы больше никогда не будете смотреть на страницы, как раньше. Я гарантирую, что, если вы станете следовать этим базовым принципам, ваша работа будет выглядеть более профессиональной, цельной и интересной. И вы почувствуете, что ваши возможности стали шире».\r\nДизайнеры и недизайнеры всего мира уже два десятилетия используют в своих работах фундаментальные принципы, изложенные Робин Уильямс. Познакомьтесь с полностью обновленным полноцветным четвертым изданием «Дизайн. Книга для недизайнеров».\r',
