    rating: 2.33,
    issueYear: '2019',
    description:
      'Книга представляет собой практическое руководство, в котором содержатся фундаментальные основы, правила и примеры составления композиционных схем верстки. Для любого графического дизайнера чрезвычайно важно понимание основ верстки, которые неразрывно связаны с почти всеми прочими аспектами графического дизайна. В этой книге подробно изложены базовые элементы и принципы композиционных схем верстки, а также содержатся объяснения относительно того, что они из себя представляют, почему так важны и каким образом могут быть наиболее эффективно использованы.\nВ книге рассмотрен не только опыт дизайнеров-студентов, но также приведены работы самых успешных и известных дизайнеров со всего света с разбором того, как они использовали основные принципы в своих работах. Благодаря тому, что в книге содержится анализ студенческих и профессиональных работ, «Школа Дизайна: Верстка» представляет собой предметное, запоминающееся и вдохновляющее руководство для будущих графических дизайнеров, а также для новичков, которые только начинают свой профессиональный путь.',
