      'В этой книге мальчик Саша со своим другом Мозгом узнает, как найти радость и где живет счастье. Как быть счастливым? Для этого нужно что-то необыкновенное или счастье прячется в самых простых вещах? А еще Вы поймете, какие правила позволят даже в трудных ситуациях не терять хорошее настроение!\r\nИ как помочь мозгу всегда оставаться активным и деятельным. О чем эта книга: Как перестать бояться ночных монстров, темноты или зубного врача? Почему важно иметь режим дня, заниматься спортом и правильно питаться? Как быстро успокоиться, если ты расстроен? Как научиться управлять мозгом, чтобы быть счастливым?',
