    rating: 3,
    issueYear: '2019',
    description:
      'О чем мечтает Ваш ребенок? Он уже знает, как достичь цели? Как исполнить свою мечту? Порой сложно объяснить детям, для чего нужно много трудиться и почему так важно не опускать руки на полпути.\r\nВ своей книге Андрей Курпатов интересно и доступно для каждого ребенка объясняет, как достичь того, что он хочет. Ваш ребенок поверит, что любая мечта осуществима, и превратится из мечтателя в деятеля!\r\nУзнайте вместе со своим ребенком:- Как развить воображение?- Что самое главное на пути к своей цели?- Сколько времени нужно, чтобы мастерски овладеть большим делом или сложным навыком?- Как обучиться всему, чему хочешь, и достичь любой цели? Читайте продолжение серии книг "Академия смысла для детей" от Андрея Курпатова.\r\nВ каждой книге серии автор рассказывает о работе мозга и его потрясающих возможностях. Строго научно и сказочно весело - именно так устроены детские книги Андрея Курпатова. Большие идеи для маленьких детей и их родителей.',
